#contact {
    min-height: calc(100vh - 7rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contactPageTitle {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.contactDesc {
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
}

.links {
    display: flex;
    flex-wrap: wrap;
    margin: 1rem 0;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
    margin: 1rem;
}

.link {
    margin: 0 0.75rem;
}

.link:hover {
    color: yellow;
    cursor: pointer;
}

a {
    color: inherit;
}


form {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    width: 18.5rem;
}

.form-group {
    width: 100%;
}

input,
#message {
    width: 100%;
    background-color: #00000040;
    border-radius: 0.3rem;
    border: 0.07rem solid rgba(255, 255, 255, 0.4);
    outline: none;
    color: white;
    padding: 0.5rem;
    font-weight: bold;
    font-size: 1rem;
    margin-bottom: 0.5rem;
}

input {
    height: 1.9rem;
}

::placeholder {
    color: white;
    font-weight: 400;
}

#message {
    height: 6.2rem;
    resize: none;
}

.contactForm {
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50vw;
    max-width: 60rem;
}

button {
    background: white;
    font-size: 1rem;
    font-weight: 600;
    width: 10rem;
    padding: 0.75rem 1rem;
    border: none;
    border-radius: 0.5rem;
    cursor: pointer;
    margin-top: 1.5rem;
}

button:hover {
    background-color: yellow;
}

.success-message {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #00000030;
    width: 100%;
    height: 12.5rem;
    border-radius: 0.3rem;
    border: 0.07rem solid rgba(255, 255, 255, 0.7);
}

.success-message p {
    margin: 0.5rem;
    padding: 0 3.7rem;
}

.error-message {
    padding: 0.3rem;
    color: white;
    background-color: rgb(255, 59, 59);
    border-radius: 0.2rem;
}

label {
    display: none;
}